  data: () => ({
    toUpperTextKey: toUpperTextKey,
    conventionNaming: conventionNaming,
    tableProps: { ...DATA_TABLE_PROPS },
    topic: TOPIC,
    rules: { ...RULES },
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    editAll: { ...POINT_EDIT_ALL_DEFAULT },
    dialog: false,
    dialogDelete: false,
    dialogMassEdit: false,
    tab: 0,
    action: '',
    network: {},
    valid: false,
    selected: [],
    editedItem: {},
    search: '',
    searchItems: [],
    editField: '',
    intervalId: null,
    pointsOfDevice: [],
    dataTable: {},
    dirs: [
      { text: 'NETWORKS', link: '/rubix-driver/networks', disabled: false },
      { text: 'POINTS VIEW', disabled: true },
    ],
    advancedView: false,
    networks: [],
    tags: [],
    panel: [0],
    typeNames: getTypeNames(),
    loadingData: false,
  }),
  computed: {
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.driverRubix;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData() {
      return this.$store.state.rdNetworks;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getAllPoints();
      }
    },
    'tags'(val) {
      // this.setTagStr(val);
      this.onChangeTag(val);
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    this.$store.commit('showOverlay');
    axios.post(`mqtt/unsubscribe`, { topic: this.topic }).finally(() => {
      this.$store.commit('hideOverlay');
      next();
    });
  },
  mounted() {
    this.getAllPoints();

    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getAllPoints();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      this.loadingData = true;
      axios
        .get(`point-server/generic/network?with_children=true&points=true`)
        .then(data => {
          this.networks = data;
          this.getDataTable(data);
        })
        .finally(() => {
          this.loadingData = false;
        });
    },
    getDataTable(networks) {
      this.dataTable = {};
      networks.forEach(network => {
        this.dataTable[network.name] = [];
        network.devices.forEach(device => {
          if (device.points.length > 0) {
            const pointsPerDevice = device.points.map(point => {
              const keys = Object.keys(point.point_store);
              if (keys && keys.length > 0) {
                keys.forEach(key => {
                  point = {
                    ...point,
                    [key]: point.point_store[key],
                    category: `DEVICE: ${device.name}`,
                    network_name: network.name,
                  };
                });
              }
              delete point.point_store;
              delete point.point_uuid;
              return point;
            });
            this.dataTable[network.name] = this.dataTable[network.name].concat(pointsPerDevice);
          }
        });
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable[item.network_name].findIndex(p => p.uuid == item.uuid);
      this.pointsOfDevice = this.dataTable[item.network_name].filter(point => point.device_uuid === item.device_uuid);
      this.editedItem = Object.assign({}, item);
      const arrTags = [];
      if (item.tags) {
        const tags = JSON.parse(item.tags);
        for (const tag in tags) {
          arrTags.push({ key: tag, value: tags[tag] });
        }
      }
      this.tags = arrTags;
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable[this.editedItem.network_name].indexOf(this.editedItem);
      this.dataTable[this.editedItem.network_name].splice(this.editedIndex, 1);
      this.dataTable[this.editedItem.network_name] = [...this.dataTable[this.editedItem.network_name]];
      await axios.delete(`point-server/generic/point/${this.editedItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(value, fieldName, item) {
      let points = _cloneDeep(this.pointsOfDevice);
      const index = points.findIndex(p => p.uuid == item.uuid);
      points.splice(index, 1);
      return points.find(p => p[fieldName] == value) != null;
    },
    async updatePoint(data, resolve, reject) {
      try {
        if (this.$refs.comboBox) this.$refs.comboBox.blur();
        this.$nextTick(async () => {
          data.fallback_value = !isNaN(parseFloat(data.fallback_value)) ? parseFloat(data.fallback_value) : null;
          const response = await axios.put(`point-server/generic/point/${data.uuid}`, data);
          const index = this.dataTable[data.network_name].findIndex(n => n.uuid == response.uuid);
          Object.assign(this.dataTable[data.network_name][index], response);
          this.$toasted.show('update success');
          this.close();
          if (resolve) {
            resolve();
          }
        });
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'nubeYellow';
      } else {
        classColor = !item.enable ? 'nubeLightGrey' : '';
      }
      return classColor;
    },
    async saveMassEdit(value, field) {
      try {
        const promises = [];
        for (const item of this.selected) {
          item[field] = value;
          promises.push(new Promise((resolve, reject) => this.updatePoint(item, resolve, reject)));
        }
        await Promise.all(promises);
        this.dialogMassEdit = false;
      } catch (ex) {}
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...POINT_EDIT_ALL_DEFAULT };
      this.tags = [];
    },
    numberChange() {
      if (!this.editAll[this.editField] && this.editField !== 'fallback_value')
        return (this.editAll[this.editField] = 0);
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    onClickRefresh() {
      this.getAllPoints();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if (!e) this.selected = [...this.selected].filter(point => point.device_uuid != items[0].device_uuid);
      else this.selected = [...this.selected].concat(items);
    },
    onChangeTag(tags) {
      if (tags.length == 0) return;
      let strTags = '';
      if (Array.isArray(tags)) {
        const objTags = {};
        for (const tag of tags) {
          if (tag.key && tag.value) {
            objTags[tag.key] = tag.value;
          }
        }
        strTags = JSON.stringify({ ...objTags });
      } else {
        strTags = JSON.stringify(tags);
      }
      this.editAll[this.editField] = strTags;
      this.editedItem.tags = strTags;
    },
    onChangeTypeName(val) {
      if (!this.typeNames.includes(val)) {
        this.tags = [];
      } else {
        const arrTags = (this.tags = []);
        const typeTags = getTypeTags(val);
        for (const tag of typeTags) {
          arrTags.push({ key: tag, value: tag });
        }
        this.tags = arrTags;
      }
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>

